import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'

export default {
	data() {
		return {
			condition:{
				type:"all",
				keyword:"",
				page: 0,
				size:6,
			},
			
			dataList : [
				//{type:"",entity:{}}
			],
			
			isSearch:false
		}
	},
	methods: {
		search(args){
			console.log(args);
			this.condition.keyword = args.value;
			this.$http.search.search(this.condition).then(res => {
				this.dataList = res.data;
				this.isSearch = true;
			}).catch(err => console.log(err))
		}
	},
	components:{
		uniSearchBar
	}
}
